import React, { Component } from 'react';
import { faExternalLinkAlt } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Gallery_1 from '../Images/gallery/cute-clothes-home.png';
// import Gallery_2 from '../Images/gallery/img-2.jpg';
// import Gallery_6 from '../Images/gallery/img-6.jpg';
import Gallery_2 from '../Images/gallery/gamer.png';
// import Gallery_3 from '../Images/gallery/img-3.jpg';
import Gallery_5 from '../Images/gallery/cute-clothes-detail.png';
// import Gallery_4 from '../Images/gallery/img-4.jpg';
import Gallery_4 from '../Images/gallery/cute-clothes-cart.png';
import Gallery_3 from '../Images/gallery/cute-clothes-admin.png';
import Gallery_6 from '../Images/gallery/gamer-detail.png';


class Porfolio extends Component {

    state = {
        onWebDesign: false,
        onPrintDesign: false,
        onWebApp: false,
        onPhotography: false,
        isOpen: false,
    }

    render() {

        const allElement = event => {
            event.preventDefault();
            this.setState({
                onWebApp: true,
                onPrintDesign: true,
                onWebDesign: true,
                onPhotography: true,
            });
        }

        const webDesign = event => {
            event.preventDefault();
            this.setState({
                onWebDesign: true,
                onPrintDesign: false,
                onWebApp: false,
                onPhotography: false,
                isOpen: true,
            });
        }

        const printDesign = event => {
            event.preventDefault();
            this.setState({
                onPrintDesign: true,
                onWebDesign: false,
                onWebApp: false,
                onPhotography: false,
                isOpen: true,
            });
        }

        // const webApp = event => {
        //     event.preventDefault();
        //     this.setState({
        //         onWebApp: true,
        //         onPrintDesign: false,
        //         onWebDesign: false,
        //         onPhotography: false,
        //         isOpen: true,
        //     });
        // }

        // const Photography = event => {
        //     event.preventDefault();
        //     this.setState({
        //         onPhotography: true,
        //         onWebApp: false,
        //         onPrintDesign: false,
        //         onWebDesign: false,
        //         isOpen: true,
        //     });
        // }


        return (
            <section id="portfolio" className="gallery-section section-padding">
                <div className="container">
                    <div className="row">
                        <div className="col col-xs-12 sortable-gallery">
                            <div className="gallery-filters">
                                <div className="section-title">
                                    <span>Portfolio</span>
                                    <h2>Proyectos realizados</h2>
                                </div>
                                <ul>
                                    <li><div className={this.state.onWebDesign && this.state.onPrintDesign ? "div-link current" : "div-link"} onClick={allElement}>All</div></li>
                                    <li><div className={this.state.onWebDesign && !this.state.onPrintDesign ? "div-link current" : "div-link"} onClick={webDesign}>SPA "gamer"</div></li>
                                    <li><div className={!this.state.onWebDesign && this.state.onPrintDesign ? "div-link current" : "div-link"} onClick={printDesign}>Ecommerce "Cute clothes"</div></li>
                                    {/* <li><a data-filter=".Web-Application" href="#" onClick={webApp}>Web Application</a></li>               
                                    <li><a data-filter=".Photography" href="#" onClick={Photography}>Photography</a></li>  */}
                                </ul>
                            </div>
                            <div className="gallery-container gallery-fancybox masonry-gallery tumho-masonary">

                                <div className={this.state.onPrintDesign ? "grid active" : "grid"
                                    && this.state.onWebApp ? "grid active" : "grid"
                                        && this.state.isOpen ? "grid" : "grid open"
                                }>
                                    <img src={Gallery_1} className="img img-responsive" alt="home ecommerce" />
                                    <div className="icon">
                                        <a href="https://cute-clothes.vercel.app/" className="view-icon">
                                            <FontAwesomeIcon icon={faExternalLinkAlt} />
                                        </a>
                                    </div>
                                </div>
                                <div className={this.state.onWebDesign ? "grid active" : "grid"
                                    && this.state.onWebApp ? "grid active" : "grid"
                                        && this.state.onPhotography ? "grid active" : "grid"
                                            && this.state.isOpen ? "grid" : "grid open"}>

                                    <img src={Gallery_2} width={"420px"} className="img img-responsive" alt="home SPA gamer" />
                                    <div className="icon">
                                        <a href="https://www.jpgamer.xyz/" className="view-icon">
                                            <FontAwesomeIcon icon={faExternalLinkAlt} />
                                        </a>
                                    </div>
                                </div>
                                <div className={this.state.onPrintDesign ? "grid active" : "grid"
                                    && this.state.onPhotography ? "grid active" : "grid"
                                        && this.state.isOpen ? "grid" : "grid open"}>
                                    <img src={Gallery_5} alt="Detail product ecommerce" className="img img-responsive" />
                                    <div className="icon">
                                        <a href="https://cute-clothes.vercel.app/" className="view-icon">
                                            <FontAwesomeIcon icon={faExternalLinkAlt} />
                                        </a>
                                    </div>
                                </div>
                                <div className={this.state.onPrintDesign ? "grid active" : "grid"
                                    && this.state.onPhotography ? "grid active" : "grid"
                                        && this.state.isOpen ? "grid" : "grid open"}>

                                    <img src={Gallery_3} alt="add product ecommmerce" className="img img-responsive" />
                                    <div className="icon">
                                        <a href="https://cute-clothes.vercel.app/" className="view-icon">
                                            <FontAwesomeIcon icon={faExternalLinkAlt} />
                                        </a>
                                    </div>
                                </div>
                                <div className={this.state.onWebDesign ? "grid active" : "grid"
                                    && this.state.onWebApp ? "grid active" : "grid"
                                        && this.state.isOpen ? "grid" : "grid open"}>

                                    <img src={Gallery_6} alt="detail game SPA gamer" className="img img-responsive" />
                                    <div className="icon">
                                        <a href="https://www.jpgamer.xyz/" className="view-icon">
                                            <FontAwesomeIcon icon={faExternalLinkAlt} />
                                        </a>
                                    </div>
                                </div>
                                <div className={this.state.onPrintDesign ? "grid active" : "grid"
                                    && this.state.onWebApp ? "grid active" : "grid"
                                        && this.state.onPhotography ? "grid active" : "grid"
                                            && this.state.isOpen ? "grid" : "grid open"}>
                                    <img src={Gallery_4} alt="cart ecommerce" className="img img-responsive" />
                                    <div className="icon">
                                        <a href="https://cute-clothes.vercel.app/" className="view-icon">
                                            <FontAwesomeIcon icon={faExternalLinkAlt} />
                                        </a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default Porfolio;