import React, { useState } from 'react';
import axios from 'axios'
import { useAlert } from 'react-alert'

const Contact = () => {
    const alertReact = useAlert()
    const [dataMail, setDataMail] = useState({
        name: '',
        email: '',
        phone: '',
        address: '',
        message: ''
    })

    const sendEmail = (e) => {
        e.preventDefault()
        if(!dataMail.message.length) return alertReact.error('Debe agregar un mensaje')
        axios({
            method: 'post',
            url: 'https://www.jpgamer.xyz/api/sendemail',
            headers: {
                'Accept': '/'
            },
            data: dataMail
        })
        .then(response => {
            if(response.data.accepted.length){
                alertReact.success('mensaje enviado')
            }
        })

    }

    const changeDataMail = (e) => {
        setDataMail({
            ...dataMail,
            [e.target.name]: e.target.value
        })
    }

    return (
        <div id="contacto" className="contact-page-area section-padding go-contact-area">
            <div className="container">
                <div className="row">
                    <div className="col-lg-5 col-md-5 col-sm-5">
                        <div className="contact-page-item">
                            <h2>Mis datos de contacto</h2>
                            <p>Puede contactarme a través de mis datos de contacto directo, desde el formulario de contacto rápido aquí a la derecha, o desde las redes sociales listadas al principio y final de página</p>
                            <div className="adress">
                                <h3>Dirección</h3>
                                <span>Merlo, Buenos Aires, Argentina</span>
                            </div>
                            <div className="phone">
                                <h3>Movil</h3>
                                <span>+5491136740098</span>
                                {/* <span>0-123-456-7890</span> */}
                            </div>
                            <div className="email">
                                <h3>Email</h3>
                                <span>julyan.patricio@gmail.com</span>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-7 col-md-7 col-sm-7">
                        <div className="contact-area contact-area-2 contact-area-3">
                            <h2>Contactar rapidamente</h2>
                            <div className="contact-form">
                                <form className="contact-validation-active" id="contact-form" onSubmit={sendEmail}>
                                    <div className="half-col">
                                        <input type="text" name="name" id="name" className="form-control" placeholder="Tu nombre" onChange={changeDataMail} />
                                    </div>
                                    <div className="half-col">
                                        <input type="email" name="email" id="email" className="form-control" placeholder="Tu email" onChange={changeDataMail} />
                                    </div>
                                    <div className="half-col">
                                        <input type="text" name="phone" id="phone" className="form-control" placeholder="Tu movil" onChange={changeDataMail} />
                                    </div>
                                    <div className="half-col">
                                        <input type="text" name="address" id="address" className="form-control" placeholder="Tu dirección" onChange={changeDataMail} />
                                    </div>
                                    <div>
                                        <textarea className="form-control" name="message" id="message" placeholder="Tu mensaje..." onChange={changeDataMail}></textarea>
                                    </div>
                                    <div className="submit-btn-wrapper">
                                        <button type="submit" className="theme-btn-s3">Submit</button>
                                        <div id="loader">
                                            <i className="fa fa-refresh fa-spin fa-3x fa-fw"></i>
                                        </div>
                                    </div>
                                    <div className="clearfix error-handling-messages">
                                        <div id="success">Thank you</div>
                                        <div id="error"> Error occurred while sending email. Please try again later. </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col col-xs-12">
                        <div className="contact-map">
                            <iframe title='google-map' src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d52495.2147175888!2d-58.78855358485681!3d-34.68118816022106!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x95bc95434093dd6b%3A0x58d832f755e4a278!2sMerlo%2C%20Provincia%20de%20Buenos%20Aires%2C%20Argentina!5e0!3m2!1ses-419!2sbd!4v1632883726732!5m2!1ses-419!2sbd" />

                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}


export default Contact;