import React, { Component } from 'react';
import { faGithub, faLinkedin, faTelegram } from '@fortawesome/free-brands-svg-icons';
import { faEnvelope } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Slides from '../Images/slide-2.jpg';

class Banner extends Component {
    render() {
        return(
            <section id="home" className="hero hero-slider-wrapper hero-style-1">
                <div className="hero-slider">
                    <div className="slide">
                        <div className="slider-image">
                            <img src={Slides} alt="background home"/>
                        </div>
                        <div className="container">
                            <div className="row">
                                <div className="col col-md-8 col-sm-12 slide-caption">
                                    <div className="slide-subtitle">
                                        <h4>Julián Rodríguez</h4>
                                    </div>
                                    <div className="slide-title">
                                        <h2>Full stack developer</h2>
                                    </div>
                                    <div className="btns">
                                        <a href="#contacto" className="theme-btn go-contact-area">Contacto</a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                {/* <div className="copy"><p>Creative Designer And Developer</p></div> */}
                <div className="social-links">
                    <ul>
                        <li>
                            <a href="https://github.com/julyanpatricio">
                                <FontAwesomeIcon icon={faGithub} />
                            </a>
                        </li>
                        <li>
                            <a href="https://www.linkedin.com/in/julianrodriguezdev/">
                                <FontAwesomeIcon icon={faLinkedin} />
                            </a>
                        </li>
                        <li>
                            <a href="https://t.me/julyanpatricio">
                                <FontAwesomeIcon icon={faTelegram} />
                            </a>
                        </li>
                        <li>
                            <a href="mailto:julyan.patricio@gmail.com">
                                <FontAwesomeIcon icon={faEnvelope} />
                            </a>
                        </li>
                    </ul>
                </div>
            </section>
        )
    }
}

export default Banner;