import React, { Component } from 'react';
import { faGithub, faLinkedin, faTelegram } from '@fortawesome/free-brands-svg-icons';
import { faEnvelope } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Logo from '../Images/logo.png';

class Footer extends Component {
    render() {
        return (
            <div className="footer-area text-center">
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                            <div className="footer-image">
                                <a href="#home">
                                    <img src={Logo} alt="logo"/>
                                </a>
                            </div>
                        </div>
                        <div className="col-12">
                            <div className="footer-menu">
                                <ul className="d-flex " >
                                    <li>
                                        <a href="https://github.com/julyanpatricio">
                                            <FontAwesomeIcon icon={faGithub} />
                                        </a>
                                    </li>
                                    <li>
                                        <a href="https://www.linkedin.com/in/julianrodriguezdev/">
                                            <FontAwesomeIcon icon={faLinkedin} />
                                        </a>
                                    </li>
                                    <li>
                                        <a href="https://t.me/julyanpatricio">
                                            <FontAwesomeIcon icon={faTelegram} />
                                        </a>
                                    </li>
                                    <li>
                                        <a href="mailto:julyan.patricio@gmail.com">
                                            <FontAwesomeIcon icon={faEnvelope} />
                                        </a>
                                    </li>
                                </ul>
                            </div>
                        </div>
                        {/* <div className="col-12">
                            <div className="footer-sub">
                                <p><i className="fa fa-copyright"></i>Copyright - 2020<span> Developed by AutWorks</span></p>
                            </div>
                        </div> */}
                    </div>
                </div>
            </div>
        );
    }
}

export default Footer;