import React, { Component } from 'react';
import aboutImg from '../Images/about.png';

class About extends Component {
    render() {
        return (
            <div className="hx-about-style-1" id="about">
                <div className="container">
                    <div className="row justify-content-between">
                        <div className="col-xl-6 col-lg-6 d-xl-flex d-lg-flex d-block align-items-center">
                            <div className="hx-about-content">
                                <div className="hx-site-title">
                                    <span>Full stack web developer</span>
                                    <h2>SOBRE MÍ</h2>
                                </div>
                                <p>Si bien soy licenciado en biotecnología, este último tiempo he redescubierto mi pasión por la programación y estoy avanzando a pasos agigantados aprendiendo todo lo que se me cruza por el camino mientras disfruto hacer lo que más me apasiona!.</p>
                                <p> Me gusta mucho disfrutar de la naturaleza y espero pronto realizar actividades como senderismo, alpinismo, etc.</p>
                                <p> Como temas de conversacion prefiero temas profundos, que invitan a pensar y reflexionar. Obviamente viniendo de las ciencias duras tambíen me gusta tratar temas relevantes a las matématicas, física y biología.</p>
                                <p> En cuanto a la personalidad, generalmente soy una persona alegre y me gusta hacer reir a los demás. Algo que me caracteriza mucho también es que soy una persona muy empatica y disfruto ayudando a los demas en lo que pueda.</p>

                                <div className="btns">
                                    <a href="https://github.com/julyanpatricio/JulyanPatricio/raw/main/CV-Rodriguez-Gomes-Julian-Patricio.pdf" className="theme-btn">Descargar CV</a>
                                </div>
                            </div>
                        </div>
                        <div className="col-xl-5 col-lg-5">
                            <div className="hx-about-img">
                                <img src={aboutImg} alt='foto de perfil julian rodriguez'/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default About;